<template>
	<div class="product-list-wrapper">
		<div class="product-list-header">
			<div class="header-left">
				<span class="solution-top">产品商城</span>
			</div>

			<div class="a-right" @click="showInput">
				如果您已重装系统或遗忘激活码，可点击此链接来查询激活码！
			</div>
		</div>
		<div class="product-list-content">
			<div
				v-for="(item, index) in productList"
				:key="index"
				class="product-item"
				:class="{ 'left-space': index % 3 !== 0 }"
				@click="checkDetail(item.id)"
			>
				<div class="name" :title="item.chName">{{ item.chName }}</div>
				<div class="eName" :title="item.enName">{{ item.enName }}</div>
				<div class="keywords" :title="item.keyWord">{{ item.keyWord }}</div>
				<img class="product-logo" :src="item.logo" />
			</div>
		</div>
		<my-dialog
			:dialogVisible="dialogVisible"
			:title="'激活码查询'"
			@closeDialog="dialogVisible = false"
			:width="1000"
		>
			<div class="header-right">
				<el-input
					placeholder="可通过输入SN查询您已购买的激活码"
					v-model="product_sn"
				></el-input>
				<el-button @click="searchActiveCode">查询激活码</el-button>
			</div>
			<!-- <div class="flex-area" v-if="codeList.length">
				<span class="title">产品SN：</span>
				<div class="value">{{ product_sn }}</div>
			</div> -->
			<div class="flex-area" v-for="(item, index) in codeList" :key="index">
				<span class="title">产品名称：</span>
				<div class="value const-name" :title="item.productSeriesName">
					{{ item.productSeriesName }}
				</div>
				<span class="title">激活码：</span>
				<div class="value">{{ item.activationCodeNo }}</div>
			</div>
			<div class="btn-area">
				<button class="square-btn " @click="dialogVisible = false">取消</button>
				<button
					class="square-btn primary split-area"
					@click="dialogVisible = false"
				>
					确定
				</button>
			</div>
		</my-dialog>
	</div>
</template>

<script>
import myDialog from "../../components/my_dialog";
import { getAllProduct } from "@/api/product";
import { baseUrl } from "../../../config";
import { getCodeBySn } from "@/api/code";

export default {
	data() {
		return {
			product_sn: "",
			baseUrl: "",
			productList: [],
			dialogVisible: false,
			codeList: [],
		};
	},
	created() {},
	mounted() {
		this.baseUrl = baseUrl;
		this.init();
	},
	components: {
		myDialog,
	},
	computed: {},
	methods: {
		checkDetail(id) {
			this.$router.push({
				name: "productDetail",
				params: {
					id,
				},
			});
		},
		init() {
			getAllProduct().then((res) => {
				res.data.data.forEach((item) => {
					item.logo = this.baseUrl + "/file/" + item.logo.replace(/.*\//g, "");
				});
				this.productList = res.data.data;
			});
		},
		showInput() {
			this.dialogVisible = true;
		},
		searchActiveCode() {
			if (!this.product_sn) {
				this.$message({
					type: "warning",
					message: "SN码不能为空",
				});
				return;
			}
			getCodeBySn({
				id: this.product_sn,
			}).then((res) => {
				if (res.data.data.length == 0) {
					// console.log('cuowu');
					this.$message({
						type: "warning",
						message: "SN码不存在",
					});
					this.codeList = [];
					return;
				}
				this.codeList = res.data.data;
				// this.dialogVisible = true;
			});
		},
	},
};
</script>

<style lang="scss">
.product-list-wrapper {
	.product-list-header {
		display: flex;
		justify-content: space-between;
		align-items: center;
		border-bottom: 1px solid #dddddd;
		padding-bottom: 15px;
		.header-left {
			background: url("../../assets/images/ico_navdot.png") no-repeat left 10px;
			.solution-top {
				display: block;
				font-size: 20px;
				line-height: 36px;
				font-family: "微软雅黑";
				padding-left: 23px;
				color: #3b3b3b;
			}
		}
		.a-right {
			cursor: pointer;
			color: #3178bf;
			font-size: 15px;
		}
		
	}
	.product-list-content {
		display: flex;
		flex-wrap: wrap;
		margin-top: 36px;
		.left-space {
			margin-left: 160px;
		}
		.product-item {
			width: 272px;
			height: 120px;
			border-radius: 2px;
			background: #fff;
			margin-bottom: 50px;
			cursor: pointer;
			border: 1px solid #92bee9;
			border-radius: 2px;
			padding: 10px 55px 0 20px;
			position: relative;
			transition: background-color 0.2s;
			.name {
				font-size: 20px;
				color: #3c3e40;
				width: 100%;
				white-space: nowrap;
				overflow: hidden;
				text-overflow: ellipsis;
			}
			.eName {
				font-size: 16px;
				color: #a2a2a2;
				padding-bottom: 10px;
				border-bottom: 1px solid #d6e4f2;
				width: 100%;
				white-space: nowrap;
				overflow: hidden;
				text-overflow: ellipsis;
			}
			.keywords {
				margin-top: 8px;
				font-size: 12px;
				color: #a2afb9;
				width: 100%;
				white-space: nowrap;
				overflow: hidden;
				text-overflow: ellipsis;
			}
			.product-logo {
				background-color: #fff;
				border-radius: 5px;
				position: absolute;
				left: 100%;
				top: 50%;
				transform: translate(-50%, -50%);
				box-shadow: 0 0 10px rgba(49, 120, 191, 0.1);
				width: 64px;
				height: 64px;
			}
		}

		.product-item:hover {
			background-color: #3178bf;
			box-shadow: 0 0 10px rgba(49, 120, 191, 0.2);
			.name {
				color: #fff;
			}
			.eName {
				color: rgba(255, 255, 255, 0.6);
			}
			.keywords {
				color: rgba(255, 255, 255, 0.8);
			}
		}
	}
	.square-btn {
		width: 120px;
		height: 44px;
		line-height: 42px;
		font-size: 18px;
		color: #313552;
		border-radius: 4px;
		background-color: rgba(66, 90, 203, 0.1);
		border: 1px solid rgba(49, 53, 82, 0.4);
		font-size: 18px;
		font-family: "alifont";
		text-align: center;
		display: inline-block;
		box-sizing: border-box;
	}
	.primary {
		background-color: #425acb;
		color: #fff;
	}
	.split-area {
		margin-left: 80px;
	}
	.btn-area {
		display: flex;
		justify-content: center;
		margin-top: 80px;
	}
	.header-right {
			display: flex;
			align-items: center;
			.el-input {
				// width: 320px;
				flex: 1;
				margin-right: -4px;
				.el-input__inner {
					height: 36px;
				}
				.el-input__inner::placeholder {
					font-size: 15px;
					line-height: 36px;
				}
			}
			.el-button {
				width: 120px;
				z-index: 10;
				background-color: #3178bf;
				color: #fff;
				height: 36px;
				line-height: 36px;
				padding: 0;
				border: none;
				border-top-left-radius: 0;
				border-bottom-left-radius: 0;
			}
		}
	.flex-area {
		display: flex;
		align-items: flex-start;
		font-size: 20px;
		margin-bottom: 25px;
		margin-top: 25px;
		.title {
			flex: 0 0 130px;
			color: #8c8c8c;
			text-align: right;
		}
		.const-name {
			width: 100px;
			flex: 0 0 100px;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
		}
		.value {
			color: #333;
			margin-left: 20px;
		}
	}
}
</style>
